@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Home */
.home{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  color: white;
  background-image: url(/car-insurance/static/media/home-background.a9e3508b.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: top;
  height: 95vh;
}

.home-elements{
  margin-top: 5%;
  transition: 3s;
}
.home-elements:hover{
  color: #d4af37;
}
.home-elements h1{
  font-size: 3rem;
}
.home-elements p{
  width: 720px;
  
}
.home-elements span{
  font-weight: bold;
}

/* Insurance */
.insurance{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  color: #fff;
  background-color: #000000;
}

.boxes{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

#icon{
  padding: 5px;
  border-radius: 50%;
  font-size: 60px;
  background-color: #000000;
  transition: 2s;
}
#icon:hover{
  background-color: #d4af37;
}
.navbar{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 80px;
    background-color: #000000;
}
.navbar-head{
    color: #fff;
    font-size: 2rem;
    text-decoration: none;
    font-weight: bold;
    margin-left: 3rem;
    transition: 0.5s;
}
.navbar-head:hover{
    color: #d4af37;
}

.menu-bars{
    margin-right: 3rem;
    font-size: 2rem;
    background: none;
}

.nav-menu{
    background: rgba(0, 0, 0, 0.9);
    width: 200px;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    position: fixed;
    top: 0;
    right: -100%;
    transition: 2s;
}

.nav-menu.active{
    right: 0;
    transition: 1s;
}

.nav-text{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    padding: 8px 0px 8px 0px;
    list-style: none;
    height: 30px;
}

.nav-text a{
    text-decoration: none;
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    width: 95%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 16px;
    border-radius: 4px;
    transition: 0.5s;
}

.nav-text a:hover{
    text-decoration: underline;
    color: #d4af37;
}

.nav-items{
    width: 100%;
}

.navbar-toggle{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 80px;
}

#burger-menu{
    color: #fff;
    transition: 0.5s;
}
#burger-menu:hover{
    text-decoration: underline;
    color: #d4af37;
}

span{
    margin-left: 16px;
}
